  .section:hover, .section:focus, .section--active {
    opacity: 1; }
  .section:focus-within {
    opacity: 1; }
  .section__heading:after {
    content: '';
    display: block;
    width: 0;
    margin: 0 auto;
    border: 0px solid transparent;
    border-bottom-color: currentColor;
    transition: border-width 0.2s 0.2s; }
  .section--active .section__heading:after {
    border-width: 1rem 2rem 2rem;
    transition: border-width 0.2s; }
  :focus-within > .section__heading:after {
    border-width: 1rem 2rem 2rem;
    transition: border-width 0.2s; }
  .section__heading:hover {
    cursor: pointer; }
  @media (min-width: 43.75rem) {
    .section__heading:after {
      position: absolute;
      right: -4rem;
      top: 50%;
      border-color: transparent;
      border-right-color: currentColor;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%); }
    .section--active .section__heading:after {
      border-width: 2rem;
      transition: border-width 0.2s; }
    :focus-within > .section__heading:after {
      border-width: 2rem;
      transition: border-width 0.2s; } }
  @media (min-width: 56.25rem) {
    :nth-of-type(2n) > .section__heading:after {
      right: auto;
      left: -4rem;
      border-color: transparent;
      border-left-color: currentColor; } }
  .section__content {
    position: relative;
    max-height: 0;
    margin: 0 calc(50% - 50vw);
    padding: 0 2rem;
    width: 100vw;
    background-color: currentColor;
    text-align: center;
    transition: max-height 0.4s;
    overflow: hidden; }
    .section--active > .section__content {
      max-height: 350px; }
    .section__content:focus-within {
      max-height: 350px; }
    .section__content > * {
      position: relative;
      color: #fff;
      max-width: 37.5rem;
      margin: 1rem auto; }
      .section__content > *:first-child {
        margin-top: 0;
        padding-top: 3rem; }
      .section__content > *:last-child {
        margin-bottom: 0;
        padding-bottom: 3rem; }
    @media (min-width: 43.75rem) {
      .section__content {
        position: absolute;
        top: 50%;
        left: calc(100% + 2rem*2);
        width: calc((100vw - 25rem - 2rem*5));
        margin: 0;
        margin: initial;
        padding: 0 4rem;
        text-align: left;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%); }
        .section--active .section__content {
          display: block; }
        .section__content:focus-within {
          display: block; }
        .section__content > * {
          max-width: 50rem;
          margin-left: 0; }
          .section__content > *:first-child {
            padding-top: 2rem; }
          .section__content > *:last-child {
            padding-bottom: 2rem; }
        .section__content:after {
          content: '';
          display: block;
          position: absolute;
          top: 50%;
          left: calc(100% + 2rem);
          width: 50px;
          height: 50px;
          border: 0px solid red;
          border-width: 0 2rem 2rem;
          border-right-color: red;
          z-index: 100; } }
    @media (min-width: 56.25rem) {
      .section__content {
        width: calc((100vw - 25rem - 2rem*4)/2); }
        :nth-of-type(2n) > .section__content {
          left: auto;
          right: calc(100% + 2rem*2);
          text-align: right; }
          :nth-of-type(2n) > .section__content > * {
            margin-right: 0;
            margin-left: auto; } }

.brace {
